	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Project Overview","depth":2},{"value":"Target Audience","depth":2},{"value":"Key Features","depth":2},{"value":"Why Spots Matters","depth":2}],"html":"<h2>Project Overview</h2>\n<p>If you are in a big city, finding a new place to eat can be overwhelming. Researching different options can take forever, and it’s always hard to know if reviews are trustworthy. SPOTS makes it quick, fun, and easy to discover local places to eat, drink, and hang out. The app connects you to restaurants, bars, and cafes that are close to your current location, and it provides you with curated recommendations based on your existing habits and preferences.</p>\n<h2>Target Audience</h2>\n<p>SPOTS is designed for people are who looking to try new places in the moment, either in their home city or abroad.</p>\n<h2>Key Features</h2>\n<p>SPOTS enables individuals to do three key things: discover new places, compare their options effortlessly, and fine tune their Spots to suit their tastes.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.91181988742965%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"The three key features of SPOTS\"\n        title=\"\"\n        src=\"/static/spots-features-97c564217d7a1fd47871159d984de299-53128.png\"\n        srcset=\"/static/spots-features-97c564217d7a1fd47871159d984de299-7266b.png 375w,\n/static/spots-features-97c564217d7a1fd47871159d984de299-0a3c1.png 750w,\n/static/spots-features-97c564217d7a1fd47871159d984de299-53128.png 1500w,\n/static/spots-features-97c564217d7a1fd47871159d984de299-91640.png 2250w,\n/static/spots-features-97c564217d7a1fd47871159d984de299-53900.png 2665w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p><strong>DISCOVER NEW PLACES</strong></p>\n<p>SPOTS shows the user a variety of nearby local Spots based on their similarity to places they like. The user can either discard the recommendations, or like them.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.9117328798887%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"App screen showing how the user discovers new Spots\"\n        title=\"\"\n        src=\"/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-53128.png\"\n        srcset=\"/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-7266b.png 375w,\n/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-0a3c1.png 750w,\n/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-53128.png 1500w,\n/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-91640.png 2250w,\n/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-ad5c5.png 3000w,\n/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-fe8a2.png 4500w,\n/static/spots-features-05-b83373a20ec7c2e77c0089ee3bd2b776-8a103.png 6469w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p><strong>COMPARE OPTIONS EFFORTLESSLY</strong></p>\n<p>The Spots that the user likes are stored in a place where they can be filtered, sorted, searched, and rated.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.9117328798887%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screen showing where liked Spots are stored\"\n        title=\"\"\n        src=\"/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-53128.png\"\n        srcset=\"/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-7266b.png 375w,\n/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-0a3c1.png 750w,\n/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-53128.png 1500w,\n/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-91640.png 2250w,\n/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-ad5c5.png 3000w,\n/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-fe8a2.png 4500w,\n/static/spots-features-06-2a313606b7fd8ef9309fa7fa785cf48e-8a103.png 6469w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p><strong>FINE TUNE SPOTS TO SUIT YOUR TASTES</strong></p>\n<p>In the detailed Spot view, users can rate Spots once they’ve visited them. The user-provided ratings feed into and help to refine the app’s future recommendations.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.9117328798887%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screen showing how user rates Spots\"\n        title=\"\"\n        src=\"/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-53128.png\"\n        srcset=\"/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-7266b.png 375w,\n/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-0a3c1.png 750w,\n/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-53128.png 1500w,\n/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-91640.png 2250w,\n/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-ad5c5.png 3000w,\n/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-fe8a2.png 4500w,\n/static/spots-features-07-0d4d18ebb4b3ec92decafcd6893373c2-8a103.png 6469w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Why Spots Matters</h2>\n<p>Although there are plenty of apps and websites that rate and recommend different food and drink venues, they can take lots of time to navigate and understand. SPOTS makes finding new places easy by showing users one nearby option at a time, and using their own preferences and social connections to support the recommendations.</p>","frontmatter":{"title":"Spots","graduate":"Danielle Sheahan","category":"Product Design","category2":"UI/UX","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/spots-main-image-ef99e9512f509e578060100fcbad1626-a7a62.jpg","srcSet":"/static/spots-main-image-ef99e9512f509e578060100fcbad1626-5a1a9.jpg 375w,\n/static/spots-main-image-ef99e9512f509e578060100fcbad1626-0908c.jpg 750w,\n/static/spots-main-image-ef99e9512f509e578060100fcbad1626-a7a62.jpg 1500w,\n/static/spots-main-image-ef99e9512f509e578060100fcbad1626-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}},"projectsInCategory":{"edges":[{"node":{"frontmatter":{"title":"aegis","graduate":"Chloe Typert-Morrison"}}},{"node":{"frontmatter":{"title":"Criket","graduate":"Alicia Lee"}}}]},"projectByStudent":{"edges":[{"node":{"frontmatter":{"title":"(Anti)socialized","graduate":"Danielle Sheahan"}}}]}},"pathContext":{"slug":"/work/spots/","name":"/Spots/","title":"Spots","graduate":"/Danielle Sheahan/","category":"/Product Design/"}}

